:root {
  font-size: 10px; }
  @media screen and (max-width: 1200px) {
    :root {
      font-size: 8px; } }
  @media screen and (max-width: 768px) {
    :root {
      font-size: 6px; } }
  @media screen and (max-width: 450px) {
    :root {
      font-size: 5px; } }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

h1 {
  margin: 0; }

body {
  font-family: 'Raleway', sans-serif;
  font-weight: 300; }

.logo {
  font-family: "Pacifico", cursive;
  letter-spacing: 1px; }
  .logo--small {
    font-size: 25px;
    color: #b9b9b9; }
  .logo--big {
    font-size: 50px; }

.section {
  width: 100%; }
  .section--about {
    background-color: #00bbbb; }
  .section--services {
    background-color: #70bfff; }
  .section--gallery {
    background-color: #ffe92f; }
  .section--subscription {
    background-image: url("../img/subscribe_bcg.jpg");
    background-position: right;
    background-repeat: no-repeat; }
    @media screen and (max-width: 1000px) {
      .section--subscription {
        background-position: left; } }
  .section--blog {
    background-color: #b07cf7; }
  .section--contact {
    background: url("../img/footer_bcg.jpg") no-repeat;
    background-position: center;
    background-size: cover; }
  .section--footer {
    background-color: #262626; }

.section__content {
  width: 80%;
  margin: 0 auto; }

.button {
  font-size: 20px;
  text-transform: uppercase;
  padding: 10px 25px;
  font-size: 20px;
  color: white;
  box-shadow: 2px 5px 12px -6px rgba(0, 0, 0, 0.75);
  cursor: pointer;
  border: none;
  transition: transform .3s ease-in-out; }
  .button--hero {
    margin-top: 20px;
    background: #ff6033; }
  .button--gallery {
    margin-top: 30px;
    background: #5585a2; }
  .button--subscribe {
    background-color: #96b92c; }
  .button--blog {
    background-color: #f72b3b; }
  .button:hover {
    transform: scale(1.1); }

header {
  width: 80%;
  position: fixed;
  display: flex;
  margin: 40px 10%;
  justify-content: space-between; }

.navigation__items {
  vertical-align: middle;
  display: flex;
  font-weight: 700; }

.navigation__item {
  vertical-align: middle;
  list-style: none;
  text-transform: uppercase;
  padding: 20px;
  font-size: 1.6rem; }

.navigation__item:last-child {
  padding-right: 0; }

.hero {
  height: 100vh;
  width: 80%;
  margin: 0 auto; }
  .hero__background {
    position: absolute;
    background-image: url(../img/header_bcg.jpg);
    height: 100vh;
    background-repeat: no-repeat;
    top: 0;
    left: 0;
    width: 100%;
    background-size: cover;
    background-position: center;
    z-index: -1; }
  .hero__content {
    padding-top: 20%; }
    .hero__content h1 {
      font-weight: 900;
      font-size: 6rem; }
    .hero__content p {
      width: 25%;
      margin-top: 10px;
      line-height: 1.5; }
      @media screen and (min-width: 769px) {
        .hero__content p {
          font-size: 17px; } }
      @media screen and (max-width: 768px) {
        .hero__content p {
          font-size: 15px; } }
  @media screen and (max-width: 1000px) {
    .hero__content {
      padding-top: 40%; } }

.about {
  display: flex;
  justify-content: space-between;
  color: white;
  padding-top: 50px;
  padding-bottom: 50px; }
  .about__image {
    width: 50%; }
    .about__image img {
      width: 100%; }
  .about__informations {
    margin: auto 0;
    width: 50%;
    margin-left: 5%; }
  .about h1 {
    font-size: 9rem; }
  .about p {
    padding-bottom: 15px;
    font-size: 17px;
    line-height: 1.5;
    width: 65%; }
  @media screen and (max-width: 1000px) {
    .about p {
      width: 100%; } }
  @media screen and (max-width: 768px) {
    .about {
      flex-direction: column;
      align-items: center; }
      .about__image {
        width: 100%; }
      .about__informations {
        width: 100%;
        text-align: justify; } }

.services {
  padding-top: 50px;
  padding-bottom: 50px;
  display: flex;
  justify-content: space-between;
  color: white; }
  .services__list {
    align-self: center;
    margin-left: 10%; }
  .services__image {
    position: relative;
    width: 50%; }
    .services__image img {
      width: 100%;
      padding: 10%; }
    .services__image .image__title {
      font-size: 9rem;
      width: 100%;
      text-align: center;
      font-weight: 700;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%); }
  @media screen and (max-width: 1000px) {
    .services__list {
      margin-left: 0; } }
  @media screen and (max-width: 850px) {
    .services {
      flex-direction: column;
      align-items: center; }
      .services__list {
        order: 2;
        width: 100%; }
      .services__image {
        width: 70%;
        padding-top: 0; }
      .services__image img {
        padding-top: 0; } }

.service {
  color: black;
  font-weight: 700;
  background-color: white;
  font-size: 20px;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  box-shadow: 3px 2px 9px 0px rgba(0, 0, 0, 0.75); }
  .service p {
    margin: auto;
    width: 80%;
    text-align: center; }
  .service span {
    width: 20%;
    height: 100%;
    border-left: 1px solid #9d9d9d;
    text-align: center; }
  @media screen and (max-width: 1000px) {
    .service {
      font-size: 16px;
      padding: 10px; } }
  @media screen and (max-width: 400px) {
    .service span img {
      width: 100%; } }

.gallery {
  padding-top: 90px;
  padding-bottom: 90px;
  text-align: center; }
  .gallery__title {
    font-size: 9rem; }
  .gallery__decription {
    font-size: 17px;
    width: 40%;
    margin: 30px auto; }
  .gallery__presentation {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-auto-rows: minmax(100px, auto);
    margin: 0 auto;
    grid-gap: 30px;
    justify-items: stretch;
    align-items: stretch;
    grid-template-areas: "peach peach peach peach blackberries blackberries" "peach peach peach peach blueberries blueberries" "milk milk milk khakki khakki khakki"; }
  .gallery .image-1 {
    grid-area: peach; }
  .gallery .image-2 {
    grid-area: blackberries; }
  .gallery .image-3 {
    grid-area: blueberries; }
  .gallery .image-4 {
    grid-area: milk; }
  .gallery .image-5 {
    grid-area: khakki; }
  @media screen and (max-width: 1000px) {
    .gallery__decription {
      width: 80%; }
    .gallery__presentation {
      grid-template-columns: repeat(6, 1fr);
      grid-template-areas: "peach peach peach peach peach peach" "blueberries blueberries blueberries blackberries blackberries blackberries" "milk milk milk khakki khakki khakki"; } }
  @media screen and (max-width: 768px) {
    .gallery__presentation {
      width: 80%;
      grid-template-columns: repeat(2, 1fr);
      grid-template-areas: "peach peach" "blueberries blueberries" "blackberries blackberries" "milk milk " "khakki khakki"; } }

.subscription {
  min-height: 500px;
  padding-top: 100px; }
  .subscription__title {
