  .subscription__description {
    font-size: 17px;
    width: 45%;
    margin: 25px 0; }
  .subscription__form input {
    font-size: 2rem;
    width: 50%;
    padding: 10px;
    margin-right: 20px; }
  @media screen and (max-width: 1000px) {
    .subscription {
      min-height: 400px;
      text-align: center; }
      .subscription__description {
        margin: 25px auto;
        padding-bottom: 30px;
        width: 70%; } }
  @media screen and (max-width: 768px) {
    .subscription {
      margin-bottom: 50px; }
      .subscription__form input {
        width: 70%;
        margin-bottom: 20px; } }
  @media screen and (max-width: 500px) {
