.blog {
  padding-top: 100px;
  padding-bottom: 100px;
  text-align: center; }
  .blog__title {
    font-size: 9rem;
    color: white;
    margin-bottom: 30px; }
  .blog__description {
    font-size: 17px;
    width: 55%;
    margin: 0 auto 30px;
    color: white;
    font-weight: 400; }

.blog__posts {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  flex-wrap: wrap;
  /*@include mediaQ(900px){
		flex-direction: column;
		.post{
			width: 3000px;
		}

	}*/ }
  .blog__posts .post {
    margin: 10px;
    position: relative;
    width: 30%;
    height: 22vw;
    background-image: url("../img/blog_raspberries.jpg");
    background-position: right;
    background-size: cover;
    background-repeat: no-repeat;
    /*&__image{
			width:100%;
			height: 100%;
		}*/ }
    .blog__posts .post__date {
      position: absolute;
      top: 5%;
      left: 5%;
      padding: 2%;
      background-color: rgba(0, 0, 0, 0.4);
      border-radius: 10%;
      color: white;
      font-weight: 700;
      font-size: 21px; }
    .blog__posts .post__summary {
      width: 100%;
      position: absolute;
      height: 30%;
