      background-image: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
      text-align: left; }
      .blog__posts .post__summary__title {
        font-size: 20px;
        color: white;
        text-shadow: black 2px 2px 2px;
        margin-top: 10px;
        margin-left: 20px;
        padding-bottom: 20px; }
      .blog__posts .post__summary__author {
